@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.1/font/bootstrap-icons.css");

:root {
  --text-color: rgb(244, 254, 255);
  --body-color: rgb(11, 153, 189);
}

body {
  margin: 0;
  background: var(--text-color);
}

.App {
  display: grid;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

/* ======== weather App ======== */
.Weather {
  background: var(--body-color);
  padding: 1.5rem;
  text-align: center;
  color: var(--text-color);
  height: 35rem;
}

.container {
  display: grid;
  gap: 5rem;
}

.top-bar {
  display: flex;
  gap: 5px;
}

.top-bar input {
  padding: 0.5rem 1rem;
  width: 12rem;
  border: 0;
  outline: none;
  border-radius: 1rem;
  text-align: center;
}

.top-bar .search-icon {
  display: flex;
  align-items: center;
  font-size: 20px;
  cursor: pointer;
  padding: 0 10px;
}

.top-bar .search-icon:hover {
  transform: scale(1.3, 1.3) rotate(-4deg);
}

.weather-image {
  display: grid;
  margin-top: 1.5rem;
  justify-content: center;
}

.weather-image img {
  width: 12.5rem;
}

.weather-temp {
  font-size: 2.5rem;
}

.weather-location {
  font-size: 1.2rem;
}

.data-container {
  display: flex;
  margin-top: 2.5rem;
  gap: 1rem;
  justify-content: space-between;
  padding: 0.5rem;
}

.element {
  display: flex;
  text-align: start;
  align-items: center;
  gap: 6px;
}

.element img {
  width: 2rem;
  height: 1.5rem;
}

.humidity-percent,
.wind-rate {
  font-size: 18px;
}

.text {
  font-size: 12px;
}

@media (max-width:991px) {
  .App {
    display: block;
  }

  .Weather {
    height: 100%;
    display: grid;
    align-items: center;
    padding: 0;
  }

  .container {
    gap: 10rem;
    padding: 0 4rem;
  }

  .top-bar {
    justify-content: center;
  }

  .top-bar input {
    width: 75%;
    height: 2rem;
    font-size: 20px;
    border-radius: 3rem;
  }

  .top-bar .search-icon {
    font-size: 35px;
  }

  .weather-image img {
    width: 25.5rem;
  }

  .weather-temp {
    font-size: 4.5rem;
  }

  .weather-location {
    font-size: 2.2rem;
  }

  .element img {
    width: 4rem;
    height: 3.5rem;
  }

  .humidity-percent,
  .wind-rate {
    font-size: 2rem;
  }

  .text {
    font-size: 1.5rem;
  }
}

@media (max-width: 789px) {
  .container {
    gap: 6rem;
    padding: 0 3rem;
  }
}

@media (max-width: 672px) {
  .container {
    gap: 3rem;
    padding: 0 2rem;
  }
}

@media (max-width: 540px) {
  .top-bar input {
    height: 1.5rem;
  }

  .top-bar .search-icon {
    font-size: 30px;
  }

  .weather-image img {
    width: 15rem;
  }

  .weather-temp {
    font-size: 3rem;
  }

  .weather-location {
    font-size: 1.8rem;
  }

  .element img {
    width: 3rem;
    height: 2.5rem;
  }

  .humidity-percent,
  .wind-rate {
    font-size: 1.5rem;
  }

  .text {
    font-size: 1rem;
  }
}


@media (max-width: 444px) {
  .container {
    gap: 0;
    padding: 0;
  }

  .top-bar {
    position: fixed;
    width: 100%;
    top: 3%;
  }

  .data-container {
    position: fixed;
    bottom: 3%;
    width: 100%;
    padding: 0;
  }

  .element:first-child {
    margin-left: 1.5rem;
  }

  .element:nth-child(2) {
    margin-right: 1.5rem;
  }
}

@media (max-width: 421px) {
  .top-bar input {
    width: 70%;
    font-size: 15px;
    height: 1.2rem;
  }

  .top-bar .search-icon {
    font-size: 28px;
  }

  .weather-image img {
    width: 12rem;
  }

  .weather-temp {
    font-size: 2.5rem;
  }

  .weather-location {
    font-size: 1.5rem;
  }

  .element img {
    width: 2.8rem;
    height: 2.2rem;
  }

  .humidity-percent,
  .wind-rate {
    font-size: 1.2rem;
  }

  .text {
    font-size: 0.8rem;
  }

  .top-bar .search-icon {
    font-size: 25px;
  }
}